*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: sans-serif;
  font-size: 25px;
  background-color: #424642;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

}
h1{
  padding: 10px;
  color: #fff;
  font-size: 40px;
  font-family: 'Times New Roman', Times, serif;
}
.container{
  margin: 20px;
  border: 2px solid red;
  border: none;
  border-radius: 15px;
  height: auto;
  width: 100%;
  background-color: #536162;
  padding-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

}

 form input{
  font-size: 20px;
  border: none;
  border-radius: 5px 0  0 5px ;
  padding: 11px;
  margin: 20px auto;
}
input:focus{
  outline: none;
  background-color: #630A10;
  color: #FCF0C8;
}
button{
  border-radius:  0 5px 5px 0;
  font-size: 20px;
  border: none;
  padding: 11px 20px;
  background-color: #911F27;
  
  
}
button:hover{
  background-color: darkred;
  cursor: pointer;
  color: white;
}
form{
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Listitem */
.list{

  color: white;
  width: 300px;
  height: 50px;
  margin: 20px auto;
  border-radius: 5px;
  background-color: #362222;
}
.list p{
  padding: 10px;
  position: relative;
  text-align: start;
}
.list p span{
  position: absolute;
  right: 10px;
  cursor: pointer;
}
.faicons{
  margin: 0 5px;
  font-size: 15px;
  color: red;
}

.list p input{
  background-color: transparent;
  border: 0;
  color: white;
  font-size: 18px;
}
.list p input:focus{
  outline: none;
}